import { Component, OnInit } from '@angular/core';
import { NavController, AlertController, Alert } from 'ionic-angular';

import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { filter } from 'rxjs/operator/filter';
import { Subject } from 'rxjs/Subject';

export interface City { 
  id: string; 
  name: string; 
  country: string; 
}

export interface House {
  id: string;
  name: string;
  description: string;
  province: string;
  city: string;
  age_from: number;
  age_to: number;
  garden: boolean;
  number_of_residents: number;
  only_female: boolean;
  only_male: boolean;
  smoker_allowed: boolean;
  price_month: number;
  residents: string;
  pets_allowed: boolean;
}

export class Garden{

  constructor(value:string, name:string) {
    this.value=value;
    this.name=name;
}

  value: string;
  name: string;
}


@Component({
  selector: 'page-search',
  templateUrl: 'search.html'
})


export class SearchPage implements OnInit {
  private itemsCollectionCity: AngularFirestoreCollection<City>;
  cities: Observable<City[]>;
  private itemsCollectionHouse: AngularFirestoreCollection<House>;
  houses: Observable<House[]>;

  selectCity: string;
  icons: string;
  garden: string;
  smokers: boolean;
  pets_allowed: string;
  room_furnished: string;
  age_from: number;
  age_to: number;
  max_price: number;
  
  gardenResult: boolean;
  pets_allowedResult: boolean;
  room_furnishedResult: boolean;

  showSearchBar: boolean;



  gardenTst = Array<Garden>();
  
      constructor(public navCtrl: NavController, public afs: AngularFirestore,public alertCtrl: AlertController) {
        this.itemsCollectionCity = this.afs.collection<City>('cities',ref => ref.where('country', '==', 'BE').orderBy('name_nl'));
        this.cities = this.itemsCollectionCity.valueChanges();

        this.icons = "male_female";

  }

  ngOnInit(){

    this.showSearchBar = true;

  }


  getSelectedItem(){
    console.log("Log event value " + this.selectCity);
    }

    getSelectedItemGarden(){
      console.log("Log event garden " + this.garden);
    }

    getSelectedSmoker() {
      console.log("Log event smokers allowed " + this.smokers);
    }

    getSelectedItemPets(){
      console.log("Log event pets allowed " + this.pets_allowed);
    }

    getSelectedRoomFurnished(){
      console.log("Log event pets allowed " + this.room_furnished);
    }

    serchCoHouses(){
      console.log("Selected city " + this.selectCity);
      if(this.selectCity === undefined || this.selectCity == '') {
          console.log("PresentAlert");
          this.presentAlert("Stad selecteren", "Gelieve een stad te selecteren om te kunnen zoeken.")
      } else {
        console.log('Dont show alert');
        console.log("age From " + this.age_from);


        //switch to check selected values and to set boolean for query.
        switch(this.garden){
          case "true": { 
            this.gardenResult = true;
            break; 
         } 
         case "false": { 
          this.gardenResult = false;
          break; 
       } default: {
         this.gardenResult = undefined;
       }
        }

        switch(this.room_furnished){
          case "true": { 
            this.room_furnishedResult = true;
            break; 
         } 
         case "false": { 
          this.room_furnishedResult = false;
          break; 
       } default: {
         this.room_furnishedResult = undefined;
       }
        }

        switch(this.pets_allowed){
          case "true": { 
            this.pets_allowedResult = true;
            break; 
         } 
         case "false": { 
          this.pets_allowedResult = false;
          break; 
       } default: {
         this.pets_allowedResult = undefined;
       }
        }

        console.log(this.gardenResult + "result");

        if(this.garden === 'true') {
          this.garden = '';
          console.log('Query house ');
          this.showSearchBar = false;
          this.itemsCollectionHouse = this.afs.collection<House>('houses', ref=> ref.where('garden', '==', true).orderBy('name'));
          this.houses = this.itemsCollectionHouse.valueChanges();
          console.log('houses ' + this.houses)
      }

      }
    }

    searchAgain(){
      this.showSearchBar = true;
    }

    presentAlert(titleText, subtitleText) {
      let alert = this.alertCtrl.create({
        title: titleText,
        subTitle: subtitleText,
        buttons: ['Dismiss']
      });
      alert.present();
    }
}



